import React from 'react';
import { graphql, compose } from 'react-apollo';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Admin.css';
import csv from './Tweener.csv';
import * as d3 from 'd3';
import gql from 'graphql-tag';
import { useMutation } from '@apollo/react-hooks';

const COMPANY_MUTATION = gql`
  mutation companyMutation(
    $name: String!
    $region: String
    $industry: String
    $linkedin: String
    $website: String
    $size: String
    $ceoName: String
  ) {
    databaseCreateCompany(
      name: $name
      region: $region
      industry: $industry
      linkedin: $linkedin
      website: $website
      size: $size
      ceoName: $ceoName
    ) @client {
      id
    }
  }
`;

class Admin extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  loadData = () => {
    d3.csv(csv, data => {
      const name = data.Company;
      const size = data.Size;
      const ceoName = data.ceoName;
      const website = data.Site;
      const linkedin = data.LI;
      const industry = data.Industry;
      const region = data.City;

      createCompany({
        variables: {
          name,
          size,
          ceoName,
          website,
          linkedin,
          industry,
          region,
        },
      });
      console.log(counter, data);
      counter += 1;
    });
  };

  render() {
    const [createCompany, { data }] = useMutation(COMPANY_MUTATION);
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1 onClick={this.loadData.bind(this)}>{this.props.title}</h1>
          <p>...</p>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Admin);
